import React from "react";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import { graphql } from "gatsby";
import Layout from "@components/layout";
import Seo from "@components/seo";
import BreadCrumb from "@components/common/breadcrumb";
import MoveForward from "@components/common/move-forward";
import MernDevelopmentScreen from "@screens/mern-development";
import { useLocation } from "@reach/router";

const MernDevelopment = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "MEAN/MERN Development Services", link: null },
  ];
  return (
    <Layout db={db}>
      <BreadCrumb items={breadcrumbLinks} />
      <MernDevelopmentScreen db={db} />
      <MoveForward db={db} />
    </Layout>
  );
};

export const Head = (props) => {
  const { data } = props;
  const logoObj = data?.allImageSharp?.edges?.find((image) => {
    return image?.node?.gatsbyImageData?.images?.fallback?.src?.includes(
      "new_logo.png"
    );
  });
  const location = useLocation();
  return (
    <Seo
      title="MERN and MEAN Stack Development Company | Justtry Technologies"
      metaTitle="MERN and MEAN Stack Development Company | Justtry Technologies"
      description="At Justtry Tech, we offer end-to-end MEAN and MERN stack development services, Our certified full-stack development team handles everything from front-end to back-end development, servers, APIs, and databases."
      image={logoObj?.node?.gatsbyImageData?.images?.fallback?.src}
      siteFirestore={data?.site?.siteMetadata?.firestore}
      slug={location.pathname.split("/")?.[1]}
    ></Seo>
  );
};

export default MernDevelopment;

export const queryData = graphql`
  query MyQuery {
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
    allImageSharp {
      edges {
        node {
          gatsbyImageData
          id
        }
      }
    }
  }
`;
